.intro {
  background-color: #FFF3E3;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.heading {
    font-family: Metropolis-Black;
    font-size: calc(24px + 5vmin);
    color: #373737;
}

.button {
  border-radius: 12px;
  border-width: 0;
  height: 7vmin;
  font-family: Metropolis-Black;
  font-size: 3.5vmin;
  text-align: center;
}

.button-text {
  align-items: center;
  justify-content: center;
  vertical-align: center;
  line-height: 4.5vmin;
  color: white;
  padding-left: 0.25vmin;
  padding-right: 0.25vmin;
  display: inline-block;
}

.button-text-two {
  align-items: center;
  justify-content: center;
  vertical-align: center;
  line-height: 4.5vmin;
  color: white;
  padding-left: 0.25vmin;
  padding-right: 0.25vmin;
  padding-top: 0.375vmin;
  display: inline-block;
}

.flex-row {
  display: flex;
  flex-direction: row;
}
.github {
  background-color: #FD6767;
  width: 23vmin;
}

.icon {
  width: 4.5vmin;
  height: 4.5vmin;
  float: left;
  padding-left: 0.625vmin;
  padding-right: 0.25vmin;
}

.linkedin{
  background-color: #589CF6;
  width: 25.25vmin;
}

.small-right-spacing {
  padding-right: 1vmin;
}

.small-top-spacing {
  padding-top: 1vmin;
}
